<script>
    import axios from "axios";

    export default {
        name: "ProductGridView",
        data(){
            return {
                id: this.$route.params.id,
                products: [],
            }
        },
        methods: {
            getAllCategoryProduct()
            {
                axios.get("http://localhost/ecom-batch-9/public/api/get-all-category-product/"+this.id).then((response)=>{
                    this.products = response.data;
                });
            }
        },
        created() {
            this.getAllCategoryProduct()
        }
    }
</script>

